import {AnyAction, applyMiddleware, combineReducers, legacy_createStore} from "redux"
import {ProfileActionType, profileReducer} from "./profileReducer";
import {MessageActionType, messageReducer} from "./messageReducer";
import {usersReducer, UsersReducerActionType} from "./usersReducer";
import {authReducer, AuthReducerActionType} from "./authReducer";
import thunkMiddleware, {ThunkAction, ThunkDispatch} from "redux-thunk"
import { reducer as formReducer } from 'redux-form'
import {appReducer, AppReducerActionType} from "./app-reducer";

let reducers = combineReducers({
        profilePage: profileReducer,
        messagePage: messageReducer,
        usersPage: usersReducer,
        auth: authReducer,
        app: appReducer,
        form: formReducer
    }
)

let store = legacy_createStore(reducers, applyMiddleware(thunkMiddleware))

export type AppRootStateType = ReturnType<typeof reducers>

//   actions   app
export type AppRootActionType = AppReducerActionType | ProfileActionType | AuthReducerActionType | MessageActionType
    | UsersReducerActionType

//    thunks
export type AppThunkType<ReturnType = void> = ThunkAction<ReturnType, AppRootStateType, unknown, AppRootActionType>

// @ts-ignore
window.store = store;
export default store;



