import {followAPI, userAPI, UserType} from "../api/api";
import {AppThunkType} from "./redux-store";

// state
let initialState: UsersPageStateType = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    startPageNumber: 1,
    isFetching: false,
    followInProgress: []
}

// reducer
export const usersReducer = (state: UsersPageStateType = initialState, action: UsersReducerActionType):UsersPageStateType => {
    switch (action.type) {
        case "FOLLOW":
            return {...state, users: state.users.map(u => u.id === action.userId ? {...u, followed: true} : u)};
        case "UNFOLLOW":
            return {...state, users: state.users.map(u => u.id === action.userId ? {...u, followed: false} : u)};
        case "SET-USERS":
            return {...state, users: action.users}
        case "SET-CURRENT-PAGE":
            return {...state, currentPage: action.currentPage}
        case "SET-START-PAGE-NUMBER":
            return {...state, startPageNumber: action.startPageNumber, currentPage: action.currentPage}
        case "SET-TOTAL-USERS-COUNT":
            return {...state, totalUsersCount: action.totalUsersCount}
        case "SET-IS-FETCHING":
            return {...state, isFetching: action.isFetching}
        case "SET-FOLLOW-IN-PROGRESS":
            return {
                ...state, followInProgress: action.isFollowInProgress
                    ? [...state.followInProgress, action.userId]
                    : state.followInProgress.filter(id => id !== action.userId)
            }
        default:
            return state
    }
}

//actions
export const follow = (userId: number) => {
    return {type: "FOLLOW", userId} as const
}
export const unFollow = (userId: number) => {
    return {type: "UNFOLLOW", userId} as const
}
export const setUsers = (users: Array<UserType>) => {
    return {type: "SET-USERS", users} as const
}
export const setStartPageNumber = (startPageNumber: number) => {
    return {type: "SET-START-PAGE-NUMBER", startPageNumber, currentPage: startPageNumber} as const
}
export const setCurrentPage = (currentPage: number) => {
    return {type: "SET-CURRENT-PAGE", currentPage} as const
}
export const setTotalUsersCount = (totalUsersCount: number) => {
    return {type: "SET-TOTAL-USERS-COUNT", totalUsersCount} as const
}
export const setIsFetching = (isFetching: boolean) => {
    return {type: "SET-IS-FETCHING", isFetching} as const
}
export const setFollowInProgress = (isFollowInProgress: boolean, userId: number) => {
    return {type: "SET-FOLLOW-IN-PROGRESS", isFollowInProgress, userId} as const
}

//thunks
export const getUsers = (currentPage: number, pageSize: number): AppThunkType => async (dispatch) => {
    dispatch(setIsFetching(true))
    const data = await userAPI.getUsers(currentPage, pageSize)
    dispatch(setUsers(data.items))
    dispatch(setTotalUsersCount(data.totalCount))
    dispatch(setIsFetching(false))
}
export const setUnFollowUser = (userId: number): AppThunkType => async (dispatch) => {
    dispatch(setFollowInProgress(true, userId))
    const data = await followAPI.deleteFollow(userId)
    if (data.resultCode === 0) {
        dispatch(unFollow(userId))
        dispatch(setFollowInProgress(false, userId))
    }
}
export const setFollowUser = (userId: number): AppThunkType => async (dispatch) => {
    dispatch(setFollowInProgress(true, userId))
    const data = await followAPI.postFollow(userId)
    if (data.resultCode === 0) {
        dispatch(follow(userId))
        dispatch(setFollowInProgress(false, userId))
    }
}

// types
export type UsersPageStateType = {
    users: Array<UserType>
    pageSize: number
    totalUsersCount: number
    currentPage: number
    startPageNumber: number
    isFetching: boolean
    followInProgress: Array<number>
}

export type  UsersReducerActionType =
    | ReturnType<typeof follow>
    | ReturnType<typeof unFollow>
    | ReturnType<typeof setUsers>
    | ReturnType<typeof setCurrentPage>
    | ReturnType<typeof setStartPageNumber>
    | ReturnType<typeof setTotalUsersCount>
    | ReturnType<typeof setIsFetching>
    | ReturnType<typeof setFollowInProgress>



