import {profileAPI} from "../api/api";
import {AppRootActionType, AppThunkType} from "./redux-store";
import {Dispatch} from "redux";

//state
let initialState: ProfilePostsStateType = {
    postsData:
        [
            {id: 1, post: 'Hi! How are you?', likeCount: 15},
            {id: 2, post: 'It`s my first post', likeCount: 25}
        ],
    profile: null,
    status: ''
}
//reducer
export const profileReducer = (state: ProfilePostsStateType = initialState, action: ProfileActionType): ProfilePostsStateType => {
    switch (action.type) {
        case "ADD-POST":
            return {...state, postsData: [...state.postsData, {id: 5, post: action.postText, likeCount: 20}]}
        case "SET-USER-PROFILE":
            return {...state, profile: action.profile};
        case "SET-USER-STATUS":
            return {...state, status: action.status};
        case "UPDATE-USER-STATUS":
            return {...state, status: action.status};
        default:
            return state;
    }
}
//actions
export const addPostAC = (postText: string) => {
    return {type: "ADD-POST", postText} as const
}
export const setUserProfile = (profile: any) => {
    return {type: "SET-USER-PROFILE", profile} as const
}
export const setUserStatus = (status: any) => {
    return {type: "SET-USER-STATUS", status} as const
}
export const setUpdateUserStatus = (status: string) => {
    return {type: "UPDATE-USER-STATUS", status} as const
}
//thunks
export const getUserProfile = (userId: number): AppThunkType => async (dispatch) => {
    const data = await profileAPI.getUserProfile(userId)
    dispatch(setUserProfile(data))
}
export const getUserStatus = (userId: number): AppThunkType => async (dispatch) => {
    const data = await profileAPI.getUserStatus(userId)
    dispatch(setUserStatus(data))
}
export const updateUserStatus = (status: string): AppThunkType => async (dispatch) => {
    const data = await profileAPI.updateUserStatus(status)
    data.resultCode === 0 && dispatch(setUpdateUserStatus(status))
}
//types
export type PostPropsType = {
    id: number,
    post: string,
    likeCount: number
}
export type ProfilePostsStateType = {
    postsData: Array<PostPropsType>
    profile: null | {}
    status: string
}
export type ProfileActionType =
    | ReturnType<typeof addPostAC>
    | ReturnType<typeof setUserProfile>
    | ReturnType<typeof setUserStatus>
    | ReturnType<typeof setUpdateUserStatus>