import {getAuthUserData} from "./authReducer";
import {AppThunkType} from "./redux-store";


//state
let initialState = {
    initialized: false
}

//reducer
export const appReducer = (state: AppReducerStateType = initialState, action: AppReducerActionType): AppReducerStateType => {
    switch (action.type) {
        case "SET-INITIALIZED-SUCCESS":
            return {...state, initialized: true};
        default:
            return state;
    }
}

//actions
export const setInitializedSuccess = () => {
    return ({type: "SET-INITIALIZED-SUCCESS"} as const)
}

//thunks
export const initialize = (): AppThunkType => async (dispatch) => {
    const res = await dispatch(getAuthUserData())
    dispatch(setInitializedSuccess())
}

//types
type AppReducerStateType = typeof initialState
export type AppReducerActionType = ReturnType<typeof setInitializedSuccess>