import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";
import {AppThunkType} from "./redux-store";

//state
let initialState: AuthReducerStateType = {
    id: null,
    email: null,
    login: null,
    isAuth: false
}

//reducer
export const authReducer = (state: AuthReducerStateType = initialState, action: AuthReducerActionType): AuthReducerStateType => {
    switch (action.type) {
        case "SET-USER-DATA":
            return {...state, ...action.payload};
        default:
            return state;
    }
}
//actions
export const setAuthUserData = (id: number | null, email: string | null, login: string | null, isAuth: boolean) => {
    return ({type: "SET-USER-DATA", payload: {id, email, login, isAuth}} as const)
}

//thunks
export const getAuthUserData = (): AppThunkType => async (dispatch) => {
    const data = await authAPI.getAuthMe()
    data.resultCode === 0 && dispatch(setAuthUserData(data.data.id, data.data.email, data.data.login, true))
}

export const login = (email: string, password: string, rememberMe: boolean): AppThunkType => async (dispatch) => {
    const data = await authAPI.login(email, password, rememberMe)
    if (data.resultCode === 0) {
        dispatch(getAuthUserData())
    } else {
        let message = data.messages.length > 0 ? data.messages : "Some error"
        dispatch(stopSubmit('login', {_error: message}))
    }
}

export const logout = (): AppThunkType => async (dispatch) => {
    const data = await authAPI.logout()
    data.resultCode === 0 && dispatch(setAuthUserData(null, null, null, false))
}

//types
type AuthReducerStateType = {
    id: number | null
    email: string | null
    login: string | null
    isAuth: boolean
}
export type  AuthReducerActionType = ReturnType<typeof setAuthUserData>
